body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
  position: relative;
}

.card__img img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__description {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  justify-content: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  flex-shrink: 0;
  place-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  align-items: center;
  gap: 17px;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.stars__icons {
  gap: 4px;
  display: flex;
}

.stars__review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
